html { font-family: sans-serif; }

html, body, #root {
  height: 100%;
  margin: unset;
}

canvas { z-index: 1 }

#menu {
  width: 20em;
  position: absolute;
  top: 0; left: 0;
  z-index: 2;
  overflow: scroll;
  max-height: calc(100% - 3em);
}

#menu.rc-menu { background-color: rgba(255, 255, 255, 0.9); }
#menu .rc-menu { background-color: transparent; }
#menu .color-box { margin-left: auto; margin-right:1em; }

#color-menu {
  position: absolute; z-index: 3; top:0; left:0;
  padding: .25em; display:inline-block;
  background-color: white;
}

.hidden { display:none !important; }

.color-box { width:1.5em; height:1.5em; display:inline-block; margin: .25em; border: 1px solid grey; }
.c0 { background-color: rgb(255, 0, 0) }
.c1 { background-color: rgb(0, 255, 0) }
.c2 { background-color: rgb(0, 0, 255) }
.c3 { background-color: rgb(255, 255, 0) }
.c4 { background-color: rgb(0, 255, 255) }
.c5 { background-color: rgb(255, 0, 255) }
